body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.wrapper {
  position: relative;
  width: 100vw;
  height: 100vh;
  /*background-color: #999999;*/
  cursor: pointer;
}

.dice-container {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  /*background-color: #ffffff;*/
  display: flex;
  flex-direction: column;
  width: 50vw;
  height: 105vw;
  max-width: 167px;
  max-height: 380px;
  justify-content: space-between;
}

.dice-up {
  position: relative;
  max-width: 167px;
  max-height: 167px;
  height: 50vw;
  border: 2px solid black;
  border-radius: 10px;
}

.dice-bottom {
  position: relative;
  max-width: 167px;
  max-height: 167px;
  height: 50vw;
  border: 2px solid black;
  border-radius: 10px;
}

.dice {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 24vw;
  font-family: arial;
}

@media (min-width: 334px) {
  .dice {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: 84px;
  }
}

@media (max-width: 200px) {
  .dice-up {
    border-radius: 7px;
  }

  .dice-bottom {
    border-radius: 7px;
  }
}

@media (max-width: 100px) {
  .dice-up {
    border: 1px solid black;
    border-radius: 5px;
  }

  .dice-bottom {
    border: 1px solid black;
    border-radius: 5px;
  }
}

@media (max-width: 30px) {
  .dice-up {
    border: 1px solid black;
    border-radius: 2px;
  }

  .dice-bottom {
    border: 1px solid black;
    border-radius: 2px;
  }
}
